class Weather {
	constructor(args){
		this.id = args.id;
		this.icon_code = args.icon_code;
		this.date = new Date(args.fcst_valid_local);
		this.day_ind = args.day_ind;
	}
}


export default {
	state: {
		count: 0,
		counting: false,
		arr: ['aaa', 'bbb', 'ccc', 'ddd', 'eee'],
		twitter_id: 'AbeShinzo',
		ymd: new Date(),
		addr: '',
		tweets: [],
		activities: [],
		weathers: [],
		personalities: [],
		big5jpname: {},
		selected_activity: {
			name: 'not selected'
		}
	},
	getters:{
		formattedDate: (state) => {
			let y = state.ymd.getFullYear();
			let m = state.ymd.getMonth() + 1;
			let d = state.ymd.getDate();
			m = (m < 10) ? '0' + m : m;
			d = (d < 10) ? '0' + d : d;
			return `${y}-${m}-${d}`;
		},
		sendingTweets: (state) => {
			state.tweets()
		}
	},
	mutations:{
		increment:(state) => state.count++,
		decrement:(state) => state.count--,
		setCounting:(state, bool) => state.counting = bool,
		add:(state) => state.arr.push(''),
		remove:(state, index) => state.arr = [...state.arr.slice(0, 1*index), ...state.arr.slice(1+1*index)],
		updateTwitterID:(state, newvalue) => state.twitter_id = newvalue,
		updateDate:(state, date) => {
			const arr = date.split('-');
			state.ymd = new Date(arr[0], arr[1], arr[2]);
		},
		updateAddr:(state, newval) => state.addr = newval,
		receiveTwitter:(state, v) => state.tweets = v,
		receiveAPI:(state, newval) => {
			state.tweets = newval.tweets;

			state.activities = [];
			console.log(newval);
			newval.recommend = newval.recommend.sort((a, b) => b.recommend_detail.recommend_ratio - a.recommend_detail.recommend_ratio);
			newval.surprise = newval.surprise.sort((a, b) => b.recommend_detail.recommend_ratio - a.recommend_detail.recommend_ratio);
			for(let i in newval.recommend){
				const act = newval.recommend[i];
				act.is_recommend = true;
				act.screen_id = 'r' + i;
				act.grayscale = (newval.recommend.length === 1) ? 0.0 : (i / (newval.recommend.length-1)) * 100.0;
				state.activities.push(act);
			}
			for(let i in newval.surprise){
				const act = newval.surprise[i];
				act.is_recommend = false;
				act.screen_id = 's' + i;
				act.grayscale = (newval.surprise.length === 1) ? 0.0 : 100 - (i / (newval.surprise.length-1)) * 100.0;
				state.activities.push(act);
			}
			state.activities.forEach((v) => v.size = Math.abs(v.recommend_detail.recommend_ratio)*33.0);
			//state.activities.forEach((v) => v.grayscale = 100.0 - Math.abs(v.recommend_detail.recommend_ratio) * 33.0);

			state.weathers = [];
			for(let i in newval.weather.forecasts){
				const d = newval.weather.forecasts[i].day;
				if(d){
					d.id = 'd' + i;
					state.weathers.push(new Weather(d));
				}
				const n = newval.weather.forecasts[i].night;
				n.id = 'n' + i;
				state.weathers.push(new Weather(n));
			}

			state.personalities = [];
			for(let i in newval.profile.personality){
				const p = newval.profile.personality[i];
				state.personalities[p.trait_id] = p.percentile;
				//state.big5jpname[p.trait_id] = p.name;
			}

			state.big5jpname = newval.label;
		},
		selectActivitySync:(state, id) => {
			const tmparr = state.activities.filter((v) => v.screen_id === id);
			if(tmparr.length > 0){
				state.selected_activity = tmparr[0];
				state.selected_activity['name'] = `${tmparr[0].recommend_detail.activity_id}:${tmparr[0].activity_detail.activity_name}`;
			}else{
				state.selected_activity = { name: 'activities not exist.' };
			}
		}
	},
	actions:{
		selectActivity: function(context, id){
			return new Promise((resolve) => {
				window.setTimeout(() => {
					context.commit('selectActivitySync', id);
					resolve();
				}, 0);
			});
		},
		invokeTwitter: function(context, args){
			return new Promise(async (resolve, reject) => {
				const config = {
					method: 'get',
					url: '/oikaze/timeline',
					responseType: 'json',
					params: {
						address: context.state.addr,
						radius: '20000',
						screen_name: context.state.twitter_id,
						countId: '5',
						recognition_flg: true,
						score: '0.6'
					}
				};
				try{
					const response = await axios.request(config);
					console.log(response);
					console.log(response.data);
					context.commit('receiveTwitter', response.data);
					resolve(response.data);
				}catch(e){
					console.log('era-dayo!!');
					console.log(e);
					reject(e);
				}
			});
		},
		invokeAPI:function(context, args){
			return new Promise(async (resolve, reject) => {
				const config = {
					method: 'post',
					url: '/oikaze/recommend',
					responseType: 'json',
					headers: {'Content-Type':'application/x-www-form-urlencoded; charset=UTF-8'},
					//headers: {'Content-Type': 'multipart/form-data'},
					//transformRequest: [(data) => window.encodeURI(JSON.stringify(data))],
					data: qs.stringify({temp:context.state.tweets})
				}
				try{
					const response = await axios.request(config);
					console.log(response);
					console.log(response.data);
					context.commit('receiveAPI', response.data);
					resolve(response.data);
				}catch(e){
					console.log('era-dayo!!');
					console.log(e);
					reject(e);
				}
			});
		},
		invokeAPIold:function(context, args){
			return new Promise(async (resolve, reject) => {
				const config = {
					method: 'get',
					url: '/oikaze',
					responseType: 'json',
					params: {
						address: context.state.addr,
						radius: '20000',
						screen_name: context.state.twitter_id,
						countId: '5',
						recognition_flg: false,
						score: '0.6'
					}
				};
				try{
					const response = await axios.request(config);
					console.log(response);
					console.log(response.data);
					context.commit('receiveAPI', response.data);
					resolve(response.data);
				}catch(e){
					console.log('era-dayo!!');
					console.log(e);
					reject(e);
				}
			});
		},
		asyncAddr:function(context, value){
			return new Promise((resolve) => window.setTimeout(() => {
				context.commit('updateAddr', value);
				resolve();
			}, 0));
		},
		incAsync:function(context){
			console.log('start inc: ', context.state.count);
			context.commit('setCounting', true);
			let v = 0;
			return new Promise((resolve) => {
				const timer = window.setInterval(() => {
					console.log('running inc: ', context.state.count, v);
					context.commit('increment');
					if(v++ > 3){
						context.commit('setCounting', false);
						window.clearInterval(timer);
						resolve('inc end.');
					}
				}, 1000);
			});
		},
		decAsync:function(context){
			console.log('start dec: ', context.state.count);
			context.commit('setCounting', true);
			let v = 0;
			return new Promise((resolve) => {
				const timer = window.setInterval(() => {
					console.log('running dec: ', context.state.count, v);
					context.commit('decrement');
					if(v++ > 3){
						context.commit('setCounting', false);
						window.clearInterval(timer);
						resolve('dec end.');
					}
				}, 1000)
			});
		},
		addAsync:function(context){
			return new Promise((resolve) => {
				window.setTimeout(() => {
					context.commit('add');
					resolve('added');
				}, 1000);
			});
		},
		removeAsync:function(context, index){
			return new Promise((resolve) => {
				window.setTimeout(() => {
					context.commit('remove', index);
					resolve('deleted');
				}, 1000);
			});
		}
	}
};



